import React from 'react';
import { PanelProps } from '@grafana/data';
import { SimpleOptions } from '../../types';
import { testIds } from '../testIds';
import { Button, ButtonGroup } from '@grafana/ui';


interface Props extends PanelProps<SimpleOptions> {} 

function onClick(e: any, data: any){
  $.ajax({
    type: 'GET',
    url: "http://localhost:8080/rest/s1/example/examples?exampleTypeEnumId=EXT_CONTRIVED&statusId=EXST_IN_DESIGN&exampleName="+data['param']+'&deviceId='+data['deviceId'],
    headers:{
      'api_key': data['api_key']
    },
      success: function (result) {
      console.log('done')
    },
      error: function (error) { 
        alert('Error')
   }

  })
}
export function SimplePanel({ // Takes in a list of props used in this example
  options, // Options declared within module.ts and standard Grafana options
  data,
  width,
  height,
  timeZone,
  timeRange,
  onChangeTimeRange,
  replaceVariables,
}: Props) {
  console.log('Panel rendered!!! ✔️');

  return (
    <div data-testid={testIds.panel.container}>
      <div> 
          <ButtonGroup>
          <span style={{'margin':'10px'}}><Button onClick={(e) => 
              onClick(e, {
                'deviceId': replaceVariables('$deviceId'), 
                'api_key': replaceVariables('$api_key'),
                'param': 'start'
              })
              } >Start</Button></span>   
          <span style={{'margin':'10px'}}><Button onClick={(e) => 
              onClick(e, {
                'deviceId': replaceVariables('$deviceId'), 
                'api_key': replaceVariables('$api_key'),
                'param': 'stop'
              })
              } >Stop</Button></span>  
            <span style={{'margin':'10px'}}><Button onClick={(e) => 
              onClick(e, {
                'deviceId': replaceVariables('$deviceId'), 
                'api_key': replaceVariables('$api_key'),
                'param': 'pause'
              })
              } >Pause</Button></span>  
            <span style={{'margin':'10px'}}><Button onClick={(e) => 
              onClick(e, {
                'deviceId': replaceVariables('$deviceId'), 
                'api_key': replaceVariables('$api_key'),
                'param': 'resume'
              })
              } >Resume</Button></span>  
         

          </ButtonGroup>
        {/*replaceVariables('$myVariable')*/} {/** Replaces any defined variable in dashboard with value of variable */}
      </div>
    </div>
  );
}
